.page-navigation {
  background: #c0c0c0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 4px;
  margin-bottom: 4px;
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
}

.nav-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0;
  background: #c0c0c0;
  padding: 0;
}

.nav-item {
  background-color: orange;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 4px 12px;
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 11px;
  font-weight: normal;
  color: #000000;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  position: relative;
  margin-right: 2px;
  line-height: normal;
}

.nav-item:hover {
  background: #dfdfdf;
}

.nav-item:active {
  border-color: #808080 #ffffff #ffffff #808080;
}

.nav-item.active {
  border-color: #808080 #ffffff #ffffff #808080;
  z-index: 1;
  padding-bottom: 6px;
  margin-bottom: -2px;
  background-color: orangered;
}

.nav-icon {
  font-size: 14px;
  display: inline-block;
}

.nav-label {
  text-transform: none;
  letter-spacing: 0;
}

.nav-separator {
  display: none;
}

.nav-decoration {
  display: none;
}

/* Mobile responsiveness */
@media (max-width: 640px) {
  .page-navigation {
    padding: 2px;
    overflow-x: auto;
  }

  .nav-container {
    min-width: fit-content;
  }

  .nav-item {
    font-size: 10px;
    padding: 3px 8px;
  }

  .nav-icon {
    font-size: 12px;
  }

  .nav-label {
    display: inline;
  }
}
