/* Enhanced Browser Page Styles - Windows 95/98 Aesthetic with Personality */

/* Base Page Container */
.browser-viewport {
  background: linear-gradient(135deg, #d4d0c8 0%, #c0c0c0 100%);
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  color: #000000;
  min-height: 100%;
  position: relative;
}

/* Subtle dot pattern overlay */
.browser-viewport::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(circle, rgba(0, 0, 0, 0.05) 1px, transparent 1px);
  background-size: 10px 10px;
  pointer-events: none;
  z-index: 1;
}

.browser-viewport .page-content {
  background: linear-gradient(180deg, #ffffff 0%, #f8f8ff 100%);
  border: 3px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow:
    inset 1px 1px 0 0 #dfdfdf,
    1px 1px 3px 0 rgba(0, 0, 0, 0.1);
  padding: 24px;
  margin: 8px;
  min-height: calc(100% - 70px);
  position: relative;
  z-index: 2;
}

/* Typography - Enhanced with colors */
.browser-viewport h1 {
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 20px;
  font-weight: bold;
  background: linear-gradient(90deg, #000080 0%, #1084d0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin: 0 0 20px 0;
  padding-bottom: 10px;
  border-bottom: 3px solid;
  border-image: linear-gradient(90deg, #000080 0%, #1084d0 100%) 1;
  text-shadow: 1px 1px 0px rgba(0, 0, 128, 0.1);
}

.browser-viewport h2 {
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 15px;
  font-weight: bold;
  color: #000080;
  margin: 18px 0 10px 0;
  padding: 4px;
  background: linear-gradient(90deg, #e6f2ff 0%, transparent 100%);
  border-left: 4px solid #1084d0;
}

.browser-viewport h3 {
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 13px;
  font-weight: bold;
  color: #333333;
  margin: 14px 0 8px 0;
  padding-left: 8px;
  border-left: 3px solid #808080;
}

.browser-viewport p {
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 11px;
  line-height: 1.7;
  color: #000000;
  margin: 10px 0;
}

.browser-viewport a {
  color: #0000ff;
  text-decoration: underline;
  cursor: pointer;
  transition: all 0.2s;
}

.browser-viewport a:hover {
  color: #ff0000;
  text-shadow: 0 0 2px rgba(255, 0, 0, 0.3);
}

/* Enhanced Window-style boxes */
.content-box {
  background: linear-gradient(180deg, #ffffff 0%, #f5f5f5 100%);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow:
    inset -1px -1px 0 0 #dfdfdf,
    2px 2px 4px 0 rgba(0, 0, 0, 0.15);
  padding: 16px;
  margin: 16px 0;
  position: relative;
  transition: transform 0.1s;
}

.content-box:hover {
  transform: translateY(-1px);
  box-shadow:
    inset -1px -1px 0 0 #dfdfdf,
    3px 3px 6px 0 rgba(0, 0, 0, 0.2);
}

.content-box-title {
  background: linear-gradient(90deg, #000080 0%, #1084d0 100%);
  color: #ffffff;
  padding: 4px 8px;
  margin: -16px -16px 12px -16px;
  font-size: 11px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

/* Enhanced Buttons */
.browser-viewport button,
.browser-viewport .button {
  background-color: orange;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 6px;
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 10px;
  color: #000000;
  cursor: pointer;
  min-width: 68px;
  text-align: center;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.1s;
  position: relative;
}

.browser-viewport button:hover,
.browser-viewport .button:hover {
  background: linear-gradient(180deg, #f0f8ff 0%, #d0e0f0 100%);
  transform: translateY(-1px);
}

.browser-viewport button:active,
.browser-viewport .button:active {
  border-color: #808080 #ffffff #ffffff #808080;
  background: linear-gradient(180deg, #e3e3e3 0%, #ffffff 100%);
  transform: translateY(0);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
}

/* Primary button style */
.browser-viewport button.primary,
.browser-viewport .button.primary {
  background: linear-gradient(180deg, #4a90e2 0%, #357abd 100%);
  color: #ffffff;
  font-weight: bold;
  border-color: #6ba3e5 #2968a3 #2968a3 #6ba3e5;
}

.browser-viewport button.primary:hover {
  background: linear-gradient(180deg, #5ba0f2 0%, #4080c0 100%);
}

.browser-viewport button:disabled {
  background: #c0c0c0;
  color: #808080;
  cursor: default;
  border-color: #dfdfdf #808080 #808080 #dfdfdf;
}

/* Enhanced Form elements */
.browser-viewport input[type='text'],
.browser-viewport input[type='email'],
.browser-viewport textarea,
.browser-viewport select {
  background: linear-gradient(180deg, #fafafa 0%, #ffffff 100%);
  border: 2px solid;
  border-color: #808080 #dfdfdf #dfdfdf #808080;
  padding: 4px 6px;
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 11px;
  color: #000000;
  transition: all 0.2s;
}

.browser-viewport input:focus,
.browser-viewport textarea:focus,
.browser-viewport select:focus {
  outline: none;
  border-color: #0080ff #4da6ff #4da6ff #0080ff;
  background: #ffffff;
  box-shadow: 0 0 3px rgba(0, 128, 255, 0.3);
}

/* Lists */
.browser-viewport ul,
.browser-viewport ol {
  margin: 10px 0 10px 24px;
  padding: 0;
}

.browser-viewport li {
  font-family: 'MS Sans Serif', 'Geneva', 'Arial', sans-serif;
  font-size: 11px;
  line-height: 1.8;
  margin: 6px 0;
  padding-left: 4px;
}

.browser-viewport li::marker {
  color: #1084d0;
}

/* Enhanced Status bar */
.status-bar {
  background: linear-gradient(180deg, #f0f0f0 0%, #d8d8d8 100%);
  border-top: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 4px 6px;
  font-size: 11px;
  display: flex;
  gap: 10px;
  margin-top: 24px;
}

.status-bar-section {
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
  padding: 3px 8px;
  flex: 1;
  background: linear-gradient(180deg, #fafafa 0%, #f0f0f0 100%);
}

/* Enhanced badges */
.badge {
  display: inline-block;
  background: linear-gradient(135deg, #808080 0%, #606060 100%);
  color: #ffffff;
  font-size: 9px;
  font-weight: bold;
  padding: 3px 6px;
  margin: 0 3px;
  border: 1px solid #404040;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

@keyframes pulse-new {
  0%,
  100% {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 0 8px rgba(0, 200, 81, 0.6);
  }
}

@keyframes pulse-hot {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* Decorative horizontal rule */
.browser-viewport hr {
  border: none;
  height: 4px;
  margin: 20px 0;
  background: linear-gradient(
    90deg,
    transparent 0%,
    #808080 25%,
    #c0c0c0 50%,
    #808080 75%,
    transparent 100%
  );
}

/* Enhanced Tables */
.browser-viewport table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  margin: 16px 0;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
  border: 2px solid #808080;
}

.browser-viewport th,
.browser-viewport td {
  border: 1px solid #c0c0c0;
  padding: 6px 10px;
  text-align: left;
  font-size: 11px;
}

.browser-viewport th {
  background: linear-gradient(180deg, #f0f0f0 0%, #d8d8d8 100%);
  font-weight: bold;
  color: #000080;
  border-bottom: 2px solid #808080;
}

.browser-viewport tr:nth-child(even) {
  background: #f8f8ff;
}

.browser-viewport tr:hover {
  background: #e8f4ff;
}

/* Scrollbar styling */
.browser-viewport ::-webkit-scrollbar {
  width: 16px;
  height: 16px;
}

.browser-viewport ::-webkit-scrollbar-track {
  background: #c0c0c0;
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
}

.browser-viewport ::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #dfdfdf 0%, #b0b0b0 100%);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
}

.browser-viewport ::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #e8e8e8 0%, #a0a0a0 100%);
}

.browser-viewport ::-webkit-scrollbar-corner {
  background: #c0c0c0;
}

/* Common retro-box style (used across multiple pages) */
.retro-box {
  background: linear-gradient(135deg, #ffffee 0%, #fff0f0 100%);
  border: 3px double #808080;
  padding: 12px;
  margin: 20px 0;
  text-align: center;
  font-weight: bold;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

/* Visitor counter (used across multiple pages) */
.visitor-counter {
  background: #000000;
  color: #00ff00;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  padding: 6px 12px;
  border: 2px inset #808080;
  display: inline-block;
  letter-spacing: 1px;
  text-shadow: 0 0 5px #00ff00;
}

/* Content section styles */
.content-section {
  margin-bottom: 25px;
}

.content-section-title {
  background: linear-gradient(90deg, #000080 0%, #1084d0 100%);
  color: white;
  padding: 8px 15px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 2px outset #808080;
  margin-bottom: 15px;
}

/* Mobile adjustments */
@media (max-width: 640px) {
  .browser-viewport .page-content {
    padding: 16px;
    margin: 4px;
  }

  .browser-viewport h1 {
    font-size: 18px;
  }

  .browser-viewport h2 {
    font-size: 14px;
  }

  .browser-viewport h3 {
    font-size: 12px;
  }

  .browser-viewport p,
  .browser-viewport li {
    font-size: 10px;
  }

  .content-box {
    padding: 12px;
    margin: 12px 0;
  }
}
